.emoji {
    font-size: .25px;
    width: 120em;
    height: 120em;
    margin: 15em 7em;
    background: #ffda6a;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    border-radius: 50%;
    position: relative
}

.emoji:hover {
    -webkit-transform: scale(1.2);
    transform: scale(1.2)
}

.emoji:after {
    position: absolute;
    bottom: -40em;
    font-size: 18em;
    width: 60em;
    left: calc(50% - 30em);
    color: #8a8a8a
}

.emoji__eyebrows,.emoji__eyes,.emoji__face,.emoji__hand,.emoji__heart,.emoji__mouth,.emoji__thumb,.emoji__tongue {
